import React, { useState, useEffect } from 'react';
import "./header.css";
import Menu from './Menu';
import Toggles from './Toggles';

const Header = () => {
    const [toggle, setToggle] = useState(false);
    const [activeSection, setActiveSection] = useState("home");
    
    const [sections] = useState([
        { title: "Home", icon: "fa-solid fa-house" },
        { title: "About", icon: "fa-solid fa-user" },
        { title: "Skills", icon: "fa-solid fa-screwdriver-wrench" },
        { title: "Projects", icon: "fa-solid fa-folder-open" },
        { title: "Résumé", icon: "fa-solid fa-file" },
        { title: "Contact", icon: "fa-solid fa-address-book" }
    ]);

    useEffect(() => {
        const scrollHeader = () => {
            const header = document.querySelector(".header");
            if(window.scrollY >= 80) header.classList.add('scroll-header'); 
            else header.classList.remove('scroll-header');
        }
        window.addEventListener("scroll", scrollHeader);
        return () => window.removeEventListener("scroll", scrollHeader);
    }, []);

    useEffect(() => {
        const handleScroll = () => {
            let current = "";
            sections.forEach(section => {
                const element = document.getElementById(section.title.toLowerCase());
                const rect = element.getBoundingClientRect();
                if (rect.top <= 120 && rect.bottom >= (window.innerHeight)/3) {
                    current = section.title.toLowerCase();
                }
            });
            setActiveSection(current);
        }
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, [sections]);

    useEffect(() => {
        const handleClickOutside = (e) => {
            if (toggle && !e.target.closest(".nav")) {
                setToggle(false);
            }
        };
        document.addEventListener("click", handleClickOutside);
        return () => document.removeEventListener("click", handleClickOutside);
    }, [toggle]);

    return (
        <header className="header">
            <nav className="nav container">
                <a href="#home" className="logo">D. Heard</a>

                <Menu sections={sections} activeSection={activeSection}
                setActiveSection={setActiveSection} toggle={toggle} setToggle={setToggle}/>
                
                <div className="nav-btns">
                    <Toggles toggle={toggle} setToggle={setToggle} />
                </div>
            </nav>
        </header>
    );
}

export default Header;