const Modal = ({ id, title, modal, toggleState, toggleTab }) => {
    return (
        <div className={toggleState === id ? "project-modal active-modal" :
         "project-modal"}>
            <div className="modal-content">
                <h4 className="modal-title">{title}</h4>
                <i className="fa-solid fa-xmark modal-close" 
                onClick={() => toggleTab(null)}></i>

                <div className="modal-desc">
                    <i className="fa-solid fa-book-open modal-icon"></i>

                    <p>{modal}</p>
                </div>
            </div>
        </div>
    )
}

export default Modal