import "./resume.css"

const Resume = () => {
    return (
        <section className="resume section" id="résumé">
            <div className="resume-bg">
                <div className="resume-container container grid">
                    <div className="resume-data">
                        <h2 className="resume-title">Résumé</h2>
                        <p className="resume-subtitle">Why Choose Me?</p>

                        <p className="resume-desc">My drive to be on the cutting edge 
                            of a growing field initially took me down a different 
                            path, but the lifelong passion I have for technology 
                            brought me here. 
                        </p>

                        <div className="resume-button">
                            <a href="https://drive.google.com/file/d/1qqafyc1zlpBBzh31dZuDru4aiCSBdrsY/view?usp=drive_link" className="button button-flex button-alt">
                                Résumé <i className="fas fa-download button-icon"></i>
                            </a>
                        </div>
                    </div>

                    <img className="resume-img" src="https://globaleducation.s3.ap-south-1.amazonaws.com/globaledu/gif/edit-form.gif" alt="" />
                </div>
            </div>
        </section>
    )
}

export default Resume