import Socials from "../home/Socials"

const Info = () => {
    return (
        <>
            <p className="contact-desc">
                I look forward to potentially working 
                with you as I continue learning and growing 
                further. Thanks for showing interest by 
                visiting my website.
            </p>

            <div className="contact-info">
                <i className="fa-solid fa-phone contact-icon"></i>

                <div>
                    <h3 className="contact-title">Phone:</h3>
                    <span className="contact-subtitle">615-314-1461</span>
                </div>
            </div>

            <div className="contact-info">
                <i className="fa-solid fa-envelope contact-icon"></i>

                <div>
                    <h3 className="contact-title">E-mail:</h3>
                    <span className="contact-subtitle">dq.heard@yahoo.com</span>
                </div>
            </div>

            <div className="contact-info">
                <i className="fa-solid fa-location-dot contact-icon"></i>

                <div>
                    <h3 className="contact-title">Location:</h3>
                    <span className="contact-subtitle">Nashville, TN</span>
                </div>
            </div>

            <div className="contact-info">
                <i className="fa-solid fa-globe contact-icon"></i>

                <div>
                    <h3 className="contact-title">Socials:</h3>
                    <div className="contact-subtitle contact-socials">
                        <Socials />
                    </div>
                </div>
            </div>
        </>
    )
}

export default Info