// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

// import required modules
import { Pagination, Navigation } from "swiper";

import { useState } from "react";
import "./projects.css"
import { Data } from "./Data"
import ReadMore from "./ReadMore"
import Modal from "./Modal"

const Projects = () => {
    const [toggleState, setToggleState] = useState(0);
    const [activeProject, setActiveProject] = useState(null);
    const [activeSlideIndex, setActiveSlideIndex] = useState(0);


    const toggleTab = (index) => {
        setToggleState(index);
        setActiveProject(Data.find(project => project.id === index));
    }
    
    return (
        <section className="project section" id="projects">
            <h2 className="section-title"> Projects</h2>
            <span className="section-subtitle">My Work So Far</span>

            <Swiper className="project-container container swiper"
                slidesPerView={1}
                grabCursor={true}
                pagination={{
                  el: ".swiper-pagination",
                  clickable: true,
                }}
                navigation={{
                  nextEl: ".swiper-button-next",
                  prevEl: ".swiper-button-prev",
                }}
                modules={[Pagination, Navigation]}

                onSlideChange={(swiper) => {
                    setActiveSlideIndex(swiper.realIndex);
                    swiper.pagination.update();
                }}
            >
                
                <div className="swiper-wrapper">
            
                    {Data.map(({id, image, title, desc, code, demo}, index) => {
                        return (
                            <SwiperSlide className="project-content grid swiper-slide" key={id}>
                                <img src={image} alt="" className="project-img"/>

                                <div className="project-data">
                                    <div className="title-container">
                                        <h3 className="project-title">{title}</h3>

                                        <ReadMore onclick={() => { if (activeSlideIndex === index) toggleTab(id); }} />
                                    </div>
                                    
                                    <p className="project-desc">{desc}</p>

                                    <a href={code} className="button button-flex button-small
                                     project-button">
                                        Code <i className="fa-solid fa-code button-icon"></i>
                                    </a>

                                    <a href={demo} className="button button-flex button-small
                                     project-button">
                                        Demo <i className="fa-solid fa-desktop button-icon"></i>
                                    </a>
                                </div>
                            </SwiperSlide>
                        )
                    })}
                </div>

                {activeProject && (
                    <Modal
                        key={activeProject.id}
                        id={activeProject.id}
                        title={activeProject.title}
                        modal={activeProject.modal}
                        toggleState={toggleState}
                        toggleTab={toggleTab}
                    />
                )}

                {/* <!-- Swiper Arrows --> */}
                <div className="swiper-button-next">
                    <i className="fa-solid fa-angle-right
                    swiper-project-icon"></i>
                </div>
                <div className="swiper-button-prev">
                    <i className="fa-solid fa-angle-left
                     swiper-project-icon"></i>
                </div>

                {/* <!-- Swiper Pagination --> */}
                <div className="swiper-pagination"></div>
            </Swiper>
        </section>
    )
}

export default Projects