const Form = () => {
    return (
        <>
            <div className="contact-inputs grid">
                <div className="contact-content">
                    <label htmlFor="" className="contact-label">Name:</label>
                    <input type="text" className="contact-input" name="name" required />
                </div>

                <div className="contact-content">
                    <label htmlFor="" className="contact-label">E-mail:</label>
                    <input type="email" className="contact-input" name="email" required />
                </div>
            </div>
        
            <div className="contact-content">
                <label htmlFor="" className="contact-label">Phone (optional):</label>
                <input type="tel" className="contact-input" name="phone" pattern="[0-9]{3}[0-9]{3}[0-9]{4}" />
            </div>

            <div className="contact-content">
                <label htmlFor="" className="contact-label">Message:</label>
                <textarea className="contact-input" name="message" cols="0" rows="7" required></textarea>
            </div>

            <button type="submit" className="button button-flex">
                Send Message
                <i className="fa-solid fa-paper-plane button-icon"></i>
            </button>

            <input type="hidden" name="_subject" value="Contact Form Submitted" />
            <input type="hidden" name="_honeypot" value="" />
        </>
    )
}

export default Form