import { useRef } from 'react'

const Cards = ({ numSkillsToShow, skills }) => {
    const ref = useRef(null);

    return (
        <>
            {skills.map((skill, index) => (
                <div key={index} ref={ref} className={`skills-content
                 ${index >= numSkillsToShow ? 'hidden-skill' : ''}`}>
                    <i className={`${skill.icon} skills-icon`}></i>
                    <h3 className="skills-title">{skill.name}</h3>
                </div>
            ))}
        </>
    )
}

export default Cards