import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';

import "./contact.css"
import Info from "./Info"
import Form from "./Form"

const Contact = () => {
    const form = useRef();

    const sendEmail = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_69uzwq7', 
        'template_retusja', form.current, '-caC61P5hVuBa1Z6x')
        .then(response => {
            if (response.ok) {
                // The request was successful
                return response.text();
            } else {
                // The request failed
                throw new Error('An error occurred while submitting the form');
            }
        }).then(text => {
            // Handle the response text
            console.log(text);
            // Display the confirmation message as an alert
            alert('Your message has been sent. You will get a reply as soon as possible.');
            e.target.reset(); // Clear the form on successful submission
        }).catch(error => {
            // Handle errors
            console.error(error);
            alert('An error occurred while submitting the form. Please try again.');
        });
    }

    return (
        <section className="contact section" id="contact">
            <h2 className="section-title">Contact Me</h2>
            <span className="section-subtitle">Let's Touch Base</span>

            <div className="contact-container container grid">
                <div className="contact-left">
                    <Info />
                </div>

                <form className="contact-form" method="POST" ref={form}
                 onSubmit={sendEmail}>
                    <Form />
                </form>
            </div>
        </section>
    )
}

export default Contact